    static getBtStatus(callback) {
        var body_dom = $('body');
        var sid      = body_dom.attr('data-sid');
        var keys = [];

        if($("#red-coupon .baitiao").length > 0) {
            $("#red-coupon .baitiao").each(function() {
                keys.push($(this).attr('data-key'));
            });

            if(sid != null && sid !="") {
                $.ajax({
                    type : "post",
                    url : VARIABLE.API.BAITIAO_URL,
                    data : {
                        "sid" : sid,
                        "key": keys.join(',')
                    },
                    dataType : "json",
                    timeout: 3000,
                    success : function(data) {
                        Loading.hideLoading('load-fullscreen');
                        if(data && data.length > 0) {
                            callback && callback(data);
                        }
                    },
                    error: function(xhr, status, e) {
                        Loading.hideLoading('load-fullscreen');
                    }
                });
            }else{
                Loading.hideLoading('load-fullscreen');
            }
        }
    }

