    // 一键领取优惠券
    static getAll(item, callback) {
        var anti_brush = $('body').attr('data-anti-brush');
        var coupon = item.find('.coupon.can-get');
        var couponIdStr = [];
        var vender_id = coupon.attr('data-venderid');

        coupon.each(function() {
            couponIdStr.push($(this).attr('data-couponid'));
        });

        var url_connect;

        if(VARIABLE.CONFIG.DEBUG) {
            url_connect = VARIABLE.DEBUG_API.GETALL;
        }else{
            if($('body').attr('data-vs') == 'weixin') {
                url_connect = VARIABLE.API.WX_GETALL;
            }else{
                url_connect = VARIABLE.API.GETALL;
            }
        }

        $.ajax({
            type : "post",
            url : url_connect,
            data : {
                "couponIdStr": couponIdStr.join(','),
                "venderId": vender_id,
                "antiBrush": anti_brush
            },
            dataType: "json",
            beforeSend: function() {
                Loading.showLoading({
                    type: 'load-fullscreen',
                    loadingTxt: '数据获取中..'
                }, function(_loadingHtml) {
                    $('.red-page').append(_loadingHtml);
                });
            },
            success:function(data){
                Loading.hideLoading('load-fullscreen');
                callback && callback(data);
            }
        });
    }

    // 进入必杀翻页
    static getRushBuyMorePage(callback) {
        var body_dom   = $('body');
        var vs         = body_dom.attr('data-vs');
        var page_now   = parseInt(body_dom.attr('data-page'), 10) + 1;
        var itemActive;

        if($('.party-item').length == 1 && $('.party-item').hasClass('active')) {
            itemActive = 0;
        }else if($('.party-item').length == 1 && !$('.party-item').hasClass('active')) {
            itemActive = 1;
        }else{
            itemActive = $('.party-item.active').index();
        }

        $.ajax({
            type : "post",
            url : VARIABLE.API.RUSH_BUY_URL,
            data : {
                "page" : page_now,
                "vs"   : vs,
                "czqgFlag": itemActive
            },
            dataType: "html",
            success:function(data){
                if(data) {
                    body_dom.attr('data-page', page_now);
                    callback && callback(data);
                }
            }
        });
    };

