    static sortGoods(callback) {
        var body_dom  = $('body');
        var filterNow = body_dom.attr('data-filter-now');
        var cateIdStr = getSelectedCateIdStr();
        var url_connect;

        if(VARIABLE.CONFIG.DEBUG) {
            url_connect = VARIABLE.DEBUG_API.NEWUSER_CONFIRM;
        }else{
            url_connect = VARIABLE.API.NEWUSER_CONFIRM;
        }

        var arg = {
            "sortFlag": body_dom.attr('data-sort-flag'),
            "vs"      : body_dom.attr('data-vs')
        };

        if(filterNow == '') {
            arg.cateIdStr = '';
            arg.firstCateIdStr = '';
        }else if(filterNow == 'cateIdStr'){
            arg.cateIdStr = cateIdStr;
            arg.firstCateIdStr = '';
        }else if(filterNow == 'firstCateIdStr') {
            arg.firstCateIdStr = $('.newuser-filter-sec li.active').attr('data-type-id').split('_')[1];
            arg.cateIdStr = '';
        }

        $.ajax({
            type : "post",
            url : url_connect,
            data : arg,
            dataType: "html",
            success:function(data){
                if(data) {
                    callback && callback(data);
                }
            }
        });
    }

