import $ from 'ZEPTO_PATH';

let rule = $('body').attr('data-promotion-string');
let sortRuleParse;
// 二分法排序
let sortArr = (arr, s, e) => {
	if(e==s){
		//就一个
		return [arr[s]];
	}else if(s==e-1){
		//就两个
		if(parseFloat(arr[s].full)<parseFloat(arr[e].full)){
			return [arr[s], arr[e]];
		}else{
			return [arr[e], arr[s]];
		}
	}
	
	var c=Math.floor((s+e)/2);
	
	//左边：s-c
	var arrL=sortArr(arr, s, c);
	//右边：c+1-e
	var arrR=sortArr(arr, c+1, e);

	//合并
	var result=[];

	while(arrL.length>0 || arrR.length>0){
		if(arrL.length==0){
			//右边都拿过来
			result=result.concat(arrR);
			break;
		}
		if(arrR.length==0){
			result=result.concat(arrL);
			break;
		}
		
		if(parseFloat(arrL[0].full)<parseFloat(arrR[0].full)){
			result.push(arrL.shift());
		}else{
			result.push(arrR.shift());
		}
	}
	
	return result;
}

if(rule) {
	let ruleParse = JSON.parse(rule);

	sortRuleParse = sortArr(ruleParse, 0, ruleParse.length - 1).reverse();
}

let main = (price) => {
	let priceTrans = parseFloat(price);

	if(!rule) {
		return 0;
	};
	
	let min = '';

	for(let i = 0; i < sortRuleParse.length; i++) {
		if(priceTrans >= sortRuleParse[i].full) {
			min = sortRuleParse[i].down;
			break;
		}
	};

	return min == ''? 0: min;
}

export default main;


