function main(slider) {
	if(slider) {
		slider.onResize && slider.onResize();
		slider.updateSlidesSize && slider.updateSlidesSize();
		slider.updateContainerSize && slider.updateContainerSize();
		slider.updateProgress && slider.updateProgress();
	}
}

module.exports = main;


