import $ from 'ZEPTO_PATH';

class Loading{
	static showLoading(opts, callback) {
		var _html = this.getLoadingHtml(opts);

		callback && callback(_html);
	}

	static hideLoading(type) {
		$('.' + type).remove();
	}

	static getLoadingHtml(opts) {
		var loadMoreHtml = [
			'<div class="'+ opts.type +' load-wrap">',
				'<div class="loading">',
	                '<div class="loading-img-box">',
	                    '<div class="loading-img"></div>',
	                    '<div class="loading-jd"></div>',
	                '</div>',
	                '<p class="loading-txt">'+ opts.loadingTxt +'</p>',
	            '</div>',
	        '</div>'
		];

		return loadMoreHtml.join('');
	}
}

module.exports = Loading;



