	static renderActNotifyStatus(data) {
		if(data && data.length) {
			for(var i = 0; i < data.length; i++) {
				$('.schedule-btn[data-actid="'+ data[i] +'"]').removeClass('red').addClass('gray').attr('report-eventparam', '取消提醒_' + data[i]).find('.notify-txt').text('取消提醒');
			}
		}

		$('.schedule-btn').attr('update', 'true');
	}

	static renderNotifyResult(btn, data) {
		if(data.code == "needLog"){
			location.href = "//plogin.m.jd.com/user/login.action?appid=100&returnurl=" + encodeURIComponent(window.location.href);
		}else if(data.code == "success"){
			if(data.appVersion >= '6.1.0') {
				// 新版本app接口
				if(btn.hasClass('red')) {
					// 开启提醒
					let dataJson = {
						"businessType": "SHANGOU",
						"showTag": "闪购",
						"remindTitle": data.androidRemindInfo.reminderTitle,
						"beginTime": data.androidRemindInfo.startTime,
						"uniqueId": 'SHANGOU' + btn.attr('data-actid'),
						"jumpStr": '{"des": "m", "srv": "", "params":{"url": "'+ data.androidRemindInfo.targetURL +'"}}',
						"imgUrl": "",
						"notifyTime": Number(data.androidRemindInfo.startTime) - 1800000,
						"extraStr1": "",
						"extraStr2": "",
						"callBackName": "setNotify",
						"callBackId": btn.attr('data-actid')
					};

					window.JDAppUnite.addReminder(JSON.stringify(dataJson));
				}else{
					// 取消提醒
					let dataJson = {
						"businessType": "SHANGOU",
						"beginTime": data.androidRemindInfo.startTime,
						"uniqueId": 'SHANGOU' + btn.attr('data-actid'),
						"callBackName": "cancelNotify",
						"callBackId": btn.attr('data-actid')
					};

					window.JDAppUnite.removeReminder(JSON.stringify(dataJson));
				}
			}else{
				if(btn.hasClass('red')) {
					Remind._normalRemind('已设置提醒，开售前30分钟提醒你！');
					btn.addClass('gray').removeClass('red').attr('report-eventparam', '取消提醒_' + btn.attr('data-actid'));
					btn.find('.notify-txt').text('取消提醒');
					if(data.androidFlag) {
						var x = window.JdAndroid.setJDReminder('SHANGOU', 'SHANGOU'+data.androidRemindInfo.reminderId, data.androidRemindInfo.reminderTitle, data.androidRemindInfo.startTime, data.androidRemindInfo.targetURL);
					}
				}else{
					Remind._normalRemind('已取消提醒，你可能会错过优惠哟~');
					btn.addClass('red').removeClass('gray').attr('report-eventparam', '提醒我_' + btn.attr('data-actid'));
					btn.find('.notify-txt').text('开售提醒');
					if(data.androidFlag) {
						var x = window.JdAndroid.cancelJDReminder('SHANGOU', 'SHANGOU'+data.androidRemindInfo.reminderId, data.androidRemindInfo.startTime);
					}
				}

				if(data.iosFlag) {
					window.location.href = data.openAppUrl;
				}
			}
		}else{
			Remind._normalRemind(data.errorMsg);
		}
	}

