	static renderRushbuyPrice(data, skus) {
		if(data == 'timeout') {
			var sku = skus.split(',');

			for(var i = 0; i < sku.length; i++) {
				var keyItem = "goodItem_" + sku[i];
				var item_group = $('.item-group');
	            var goodItem = $("a[data-goods-id='"+keyItem+"']", item_group);

				$('.old-price', goodItem).remove();
	            $('.new-price', goodItem).html('<span class="new-price-num">暂无价格</span>');
			}
			return;
		}

		for(var i = 0; i < data.length; i++){
	        var jdPrice = "";
	        var oldPrice = "";

	        if(data[i].p > 0) {
	            jdPrice = util.formatPrice(data[i].p);
	            oldPrice = util.formatPrice(data[i].op);
	        } else {
	            jdPrice = "暂无价格";
	        }

	        var value = data[i].id.split('_');

	        if(value.length == 1) {
	            var tempSku = value[0];
	            var keyItem = "goodItem_" + tempSku;
				var item_group = $('.item-group');
	            var goodItem = $("a[data-goods-id='"+keyItem+"']", item_group);
	            var actId = goodItem.attr('data-actid');

	            if(jdPrice == "暂无价格"){
	                // 已抢光
                	$('.goods-img-wraper', goodItem).addClass('dry-up');

                	var rushBuyItem = goodItem.closest('.rush-buy-item');

                	if(rushBuyItem.find('.a-act').length) {
                		var itemIndex = goodItem.closest('.page-wraper').index() * 50 + rushBuyItem.index() + 1;
                		var actid = rushBuyItem.find('.a-act').attr('data-actid');
                		
                		goodItem.attr('href', rushBuyItem.find('.a-act').attr('href'));
                		rushBuyItem.find('.item-con a').attr('report-eventid', 'MFlashbuy_MoreDiscount').attr('report-eventparam', tempSku + '_' + actid + '_' + itemIndex + 1);
                	}else{
                		goodItem.attr('href', '//red.m.jd.com');
                	}

					// 更改按钮样式
					$('.btn', goodItem).removeClass('rush-buy').addClass('more-preferential').text('更多优惠');
	            }else{
	            	if(VARIABLE.CONFIG.PRICE_SWITCH == 'off') {
	            		$('.old-price', goodItem).remove();
	            		$('.new-price', goodItem).html('<span class="new-price-num">暂无价格</span>');
	            	}else{
	            		$('.new-price', goodItem).html('<span class="price-type">¥&nbsp;</span><span class="new-price-num">'+ jdPrice +'</span>');
	                	// $('.old-price', goodItem).html('<span class="price-type">市场价</span><span class="old-price-num">¥&nbsp;'+ oldPrice +'</span>');
	                	$('.old-price', goodItem).html('<span class="old-price-num">¥&nbsp;'+ oldPrice +'</span>');
	            	}
	            }
	        }
	    }
	}

