window.cancelNotifyDetail = function(ret, callBackId) {
	let btn = $('.btn-start-remind');

	if(ret == 0) {
		Remind._normalRemind('取消设置失败');
	}else{
		Remind._normalRemind('已取消提醒，你可能会错过优惠哟~');
		btn.removeClass('gray').addClass('red').attr('report-eventparam', '提醒我_' + btn.attr('data-actid'));
		btn.find('em').text('开售提醒');
	}
}

module.exports = View;



