import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import cookie from 'UTILS_PATH/cookie';
import util from 'UTILS_PATH/helper';

let headerInit = () => {
	var mchb = new MCommonHeaderBottom();
    var _queryUrl = util.queryUrl(window.location.href);

    if(_queryUrl.todayheadlines == '1') {
        cookie('todayheadlines', '1');
    };

    if(_queryUrl.from_react == 'true' && _queryUrl.usescroll != 'true') {
        $('#header .jd-nav, #header .red-nav-box').remove();
        return refresh_header_height();
    }

    if(VARIABLE.CONFIG.VS == 'jdapp' || VARIABLE.CONFIG.VS == 'weixin' || VARIABLE.CONFIG.VS == 'wq' || cookie('todayheadlines') == '1') {
        return refresh_header_height();
    };

    /*公共头部*/
    var headerArg = {
    	hrederId : 'jd-header',
    	title: VARIABLE.CONFIG.TITLE,
    	sid : VARIABLE.CONFIG.SID,
    	isShowShortCut : false,
    	selectedShortCut : '4',
    	call: function() {
    		refresh_header_height();
            header_addevent();
    	}
    }

    mchb.header(headerArg);
}

function refresh_header_height() {
    if(!VARIABLE.CONFIG.ANIMA_SCROLL) return;

    var header     = $('#header');
    var scroll_top = header.height();
    
    $('#scroll-view').css('top', scroll_top + 'px');
    VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
}

function header_addevent() {
    $('#jd-nav div[report-eventid="MDownLoadFloat_Close"]').click(refresh_header_height);
    $('#jd-nav div[report-eventid="MCommonHead_NavigateButton"]').click(refresh_header_height);
}

module.exports = {
	headerInit : headerInit
};



