import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable');
var util = require('UTILS_PATH/helper');

function start() {
	if(!VARIABLE.CONFIG.MOBILE) {
		// pc
		$('.red-page').addClass('page-border').addClass('width640');
		VARIABLE.CONFIG.ANIMA_SCROLL = false;
	}else{
		// 移动端
		$('.red-page').addClass('mobile');
	}

	if(util.queryUrl(window.location.href).usescroll) {
		try{
			document.domain = 'jd.com';
		}catch(e) {

		}
		$('.red-page').removeClass('page-border').removeClass('width640');
		VARIABLE.CONFIG.ANIMA_SCROLL = false;
	}

	if(!$('#footer-nav').length) {
		$('#scroller').removeClass('pb85');
	}

	if(!VARIABLE.CONFIG.ANIMA_SCROLL) {
		$('html').removeClass('body_overflow');
		$('body').removeClass('body_overflow');

		$('.red-page').removeClass('anima-scroll-page');
		$('#scroll-view').removeClass('anima-scroll-view');
	}
}

module.exports = start;


