import $ from 'ZEPTO_PATH';

class Remind{
	static _normalRemind(text) {
		$('.remind').remove();
		var remind_html = '<div class="remind">'+ text +'</div>';

		$('.red-page').append(remind_html);

		setTimeout(function() {
			$('.remind').addClass('animated fadeOut');
			setTimeout(function() {
				$('.remind').remove();
			}, 400);
		}, 1000);
	}

	static _couponsRemind(bolean, text) {
		var remind_html;
		var coupons_remind_type;
		var remind_text = text;

		$('.coupons_remind').remove();
		if(bolean) {
			coupons_remind_type = 'coupons_seccess';
		}else{
			coupons_remind_type = 'coupons_faild';
		}

		remind_html = [
			'<div class="coupons_remind">',
	            '<i class="'+ coupons_remind_type +'"></i>',
	            '<p>'+ text +'</p>',
	        '</div>'
		];

		$('.red-page').append(remind_html.join(''));
		setTimeout(function() {
			$('.coupons_remind').addClass('animated fadeOut');
			setTimeout(function() {
				$('.coupons_remind').remove();
			}, 400);
		}, 1000);
	}
}

module.exports = Remind;


